/*

 tap   singleTap   doubleTap
 hold
 drag    dragLeft    dragRight   dragUp   dragDown
 swipe   swipeLeft   swipeRight    swipeUp   swipeDown

 pinch   pinchStart    pinchMove    pinchEnd    pinchIn   pinchOut
 rotate
 */
let handlers = {
  // tapHandler: null,
  // singleTapHandler: null,
  // doubleTapHandler: null,
  // holdHandler: null,
  // swipeHandler: null,
  // swipeUpHandler: null,
  // swipeRightHandler: null,
  // swipeDownHandler: null,
  // swipeLeftHandler: null,
  // pinchStartHandler: null,
  // pinchMoveHandler: null,
  // pinchEndHandler: null,
  // pinchHandler: null
}
let Events = {}

let globalNativeEvent = {}
let globalEventParams = null

let bindCustomEvents = (el, eventName) => {
  let eventNames = ['tap', 'singleTap', 'doubleTap', 'hold', 'swipe', 'swipeUp', 'swipeRight', 'swipeDown', 'swipeLeft', 'pinch', 'pinchStart', 'pinchMove', 'pinchEnd']
  let handler = ''
  eventNames.forEach(eventName => {
    handler = `${eventName}Handler`
    if (!handlers[handler]) {
      handlers[handler] = null
    }
    Events[eventName] = new Event(eventName)
  })

  el.addEventListener(eventName, (event) => {
    event.stopPropagation()
    let handler = `${eventName}Handler`

    if (handlers[handler]) {
      if (globalEventParams) {
        if (Array.isArray(globalEventParams)) {
          handlers[handler](globalEventParams[0])
        } else if (typeof globalEventParams === 'object') {
        }
      } else {
        if (globalNativeEvent) {
          handlers[handler](packingCustomEvent(event))
        } else {
          handlers[handler](event)
        }
      }
    }
  }, false)
}

let bindTouchEvents = (el) => {
  let startTime = 0
  let lastTouchTime = 0 // 
  let startX, currentX, startY, currentY

  let isMove = false // 
  let ishold = false // 
  let isdoubleTap = false // 

  let holdDelay = 750 // 750touchstart-touchend750
  let doubleTapDelay = 250 // 250touchstart250
  let moveThreshold = 30 // 301.2.

// ID
  let holdTimeout = null
  let singleTapTimeout = null
// 
  let hold = () => {
    ishold = true
    el.dispatchEvent(Events.hold)
    lastTouchTime = 0
  }

  let singleTap = () => {
    el.dispatchEvent(Events.singleTap)
    el.dispatchEvent(Events.tap)
  }
//
  let clearhold = () => {
    if (holdTimeout) {
      window.clearTimeout(holdTimeout)
      ishold = false
      holdTimeout = null
    }
  }
  let _getTime = () => Date.now()

  let fingerNum = 1 // 

  el.addEventListener('touchstart', (event) => {
    globalNativeEvent = null
    event.preventDefault()
    fingerNum = event.touches.length

    if (fingerNum === 2) {
      globalNativeEvent = event
      el.dispatchEvent(Events.pinchStart)
      return
    }

    startTime = _getTime()
    startX = event.touches[0].pageX
    startY = event.touches[0].pageY

    // Timeout ID
    singleTapTimeout && window.clearTimeout(singleTapTimeout)

    // 
    if (startTime - lastTouchTime > 0 && startTime - lastTouchTime <= doubleTapDelay) {
      isdoubleTap = true
    }

    lastTouchTime = _getTime()
    // 
    holdTimeout = window.setTimeout(hold, holdDelay)
  })

  el.addEventListener('touchmove', (event) => {
    globalNativeEvent = null
    event.stopPropagation()
    event.preventDefault()
    fingerNum = event.touches.length

    if (fingerNum === 2) {
      globalNativeEvent = event
      el.dispatchEvent(Events.pinchMove)
      // return
    }

    currentX = event.touches[0].pageX
    currentY = event.touches[0].pageY

    if (Math.abs(currentX - startX) > moveThreshold || Math.abs(currentY - startY) > moveThreshold) {
      isMove = true
    }
    clearhold() // 
  })

  el.addEventListener('touchend', (event) => {
    globalNativeEvent = null
    if (fingerNum === 2) {
      globalNativeEvent = event
      el.dispatchEvent(Event.pinchEnd)
      return
    }

    if (isMove) {
      el.dispatchEvent(Events.swipe)

      if (currentX < startX) { // 
        if (Math.abs(currentX - startX) > Math.abs(currentY - startY)) { // 
          el.dispatchEvent(Events.swipeLeft)
        } else {
          if (currentY < startY) { // 
            el.dispatchEvent(Events.swipeUp)
          } else {
            el.dispatchEvent(Events.swipeDown)
          }
        }
      } else { // 
        if (Math.abs(currentX - startX) > Math.abs(currentY - startY)) { // 
          el.dispatchEvent(Events.swipeRight)
        } else {
          if (currentY < startY) { // 
            el.dispatchEvent(Events.swipeUp)
          } else {
            el.dispatchEvent(Events.swipeDown)
          }
        }
      }
    } else { // 
      if (ishold) { // 
      } else { // 
        el.dispatchEvent(Events.tap)
        if (isdoubleTap) {
          globalNativeEvent = event
          el.dispatchEvent(Events.doubleTap)
          el.dispatchEvent(Events.tap)
          lastTouchTime = 0
        } else {
          singleTapTimeout = window.setTimeout(singleTap, doubleTapDelay)
        }
      }

      isdoubleTap = false
      clearhold() // 
    }

    isMove = false
  })
}

let packingCustomEvent = (customEvent) => {
  ['touches', 'targetTouches', 'changedTouches'].forEach(eventName => {
    customEvent[eventName] = globalNativeEvent[eventName]
  })

  return customEvent
}

export default {
  install (Vue, options) {
    Vue.directive('event' || '@', {
      bind (el, binding) {
        if (typeof binding.value === 'function') {
          handlers[`${binding.arg}Handler`] = binding.value
        } else if (typeof binding.value === 'object') {
          if (!binding.value.method) {
            console.error('')
            return
          } else {
            handlers[`${binding.arg}Handler`] = binding.value.method
            if (binding.value.params) {
              globalEventParams = binding.value.params
            }
          }
        } else {
          console.error('')
          return
        }
        bindTouchEvents(el)
        bindCustomEvents(el, binding.arg)
      },
      unbind () {
      }
    })
  }
}



