import Toast from './Toast'

export default {
  install (Vue, options = {}) {
    const ToastConstructor = Vue.extend(Toast)

    // Toast
    ToastConstructor.prototype.close = () => {
      this.close()
    }

    /*
    VueToastDOM
    VueVueVuethis.$toast
     */
    Vue.prototype.$toast = (option = {}) => {
      // Toastdiv
      let instance = new ToastConstructor().$mount(document.createElement('div'))
      let duration = option.duration || options.duration || 2000

      // Vueoptionmessage,optionmessagetoast
      instance.message = typeof option === 'string' ? option : option.message

      //
      if (typeof option === 'object' && option.position && (option.position === 'top' || option.position === 'middle' || option.position === 'bottom')) {
        instance.position = option.position
      }

      // toastDOMbody
      document.body.appendChild(instance.$el)

      // 
      window.setTimeout(() => {
        instance.close()
      }, duration)
    }
  }
}



